@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;600;700;800&display=swap');

body {
    font-family: 'Poppins', Arial, Helvetica, sans-serif;
    margin: 0;
}

header {
    background-image: linear-gradient(to right,rgb(123, 20, 179), rgb(154, 86, 190));
    color: white;
    display: flex;
    align-items: center;
    height: 50px;
    padding: 25px 30px;
}

header img {
    height: 100%;
    margin-right: 1rem;
}

header h3 {
    margin-right: auto;
}

header h4 {
    font-weight: 500;
}

.main {
    display: grid;
    grid-template: 40px 40px/ 1fr 1fr;
    gap: 1rem;
    padding: 30px;
    max-width: 700px;
    margin: auto;
}

.main button {
    grid-column: 1 / -1;
    background-image: linear-gradient(to right,rgb(123, 20, 179), rgb(154, 86, 190));
    color: white;
}

.main input {
    padding: 0.5rem;
}

.main * {
    border-radius: 5px;
    border: 1px solid rgb(190, 188, 188);
}

.meme {
    height: auto;
    max-width: 700px;
    margin-inline: auto;
    padding-inline: 30px;
    position: relative;
}

.meme p {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 4rem;
    color: white;
    text-shadow: -3px -3px 0 #000, 3px -3px 0 #000, -3px 3px 0 #000, 3px 3px 0 #000;
    margin: 1rem 0;
    width: 80%;
    height: auto;
    overflow-wrap: break-word;
    text-align: center;
}

.meme img {
    object-fit: fill;
    width: 100%;
}

.topText {
    top: 0;
}

.bottomText {
    bottom: 0;
}